import React, { Component } from 'react';
import Iframe from 'react-iframe';
import '../map/map.css';

class Map extends React.Component {
    render() {
        return (
            <div className="map-container">
                <Iframe url="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d2244.996984934027!2d37.731490416157044!3d55.75855458055487!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x46b535538777cd87%3A0x6cffb313590253ba!2z0YPQuy4g0JHRg9GA0LDQutC-0LLQsCwgMjcsINCc0L7RgdC60LLQsCwg0KDQvtGB0YHQuNGPLCAxMDUxMTg!5e0!3m2!1sru!2sbg!4v1534530095101"
                        width="100%"
                        height="100%"
                        id="myMap"
                        className="map"
                        display="initial"
                        position="relative"
                />
            </div>
        );
    }
}

export default Map;


